import Swiper, { Navigation, Pagination } from 'swiper';
import buildSliders from './buildSliders';
import 'ScssComponents/_efficient_work.scss';

const efficientWorkSlider = () => {
	const sliderClass = '.efficientWorkSlider';
	buildSliders(sliderClass);

	let slideEl = document.querySelectorAll(sliderClass);

	if (typeof (slideEl) !== 'undefined' && slideEl != null) {
		const $currentSlideIndex = document.querySelector('.efficientWorkCurrent');
		const $totalSlideIndex = document.querySelector('.efficientWorkTotal');

		let efficientWorkSliderEl = new Swiper(sliderClass, {
			modules: [Navigation, Pagination],
			observer: true,
			observeParents: true,
			speed: 800,
			loop: true,

			watchOverflow: true,
			watchSlidesProgress: true,
			centeredSlides: false,

			navigation: {
				nextEl: '.efficient_work_navigation .efficient_work_arrow--next',
				prevEl: '.efficient_work_navigation .efficient_work_arrow--prev',
			},

			pagination: {
				el: '.efficientWorkProgress',
				type: 'progressbar',
				clickable: true,
			},

			breakpoints: {
				320: {
					slidesPerView: 1,
					spaceBetween: 20,
				},
				768: {
					slidesPerView: 1.5,
					spaceBetween: 20,
				},
				1024: {
					slidesPerView: 'auto',
					spaceBetween: 0,
				},
			},

			on: {
				init: (slider) => {
					const currentSlider = document.querySelector(sliderClass);
					const index = currentSlider.querySelectorAll('.swiper-slide:not(.swiper-slide-duplicate)').length;
					$totalSlideIndex.innerHTML = index >= 10 ? index : `0${index}`;
				},
				slideChange: (slider) => {
					const index = slider.realIndex + 1;
					$currentSlideIndex.innerHTML = index >= 10 ? index : `0${index}`;
				},
			},
		});
	}
};

export default efficientWorkSlider;
