const subscriptionForm = () => {
	const $success = document.querySelector('.subscriptionSuccess');
	const $fields = document.querySelectorAll('.subscriptionFormField');
	const errorState = 'subscription_form__field--error_state';
	const typingState = 'subscription_form__field--typing_state';
	const errorMessage = 'Incorrect e-mail';

	const isValid = (e, $input) => {
		e.preventDefault();

		if (!$input.getAttribute('type') === 'email') return null;

		const { value } = $input;

		const res = String(value)
			.toLowerCase()
			.match(/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/);

		return res;
	};

	const throwError = ($wrapper, $input) => {
		$wrapper.classList.add(errorState);
		$input.value = '';
		$input.setAttribute('placeholder', errorMessage);
	};

	const handleSubmit = ($wrapper) => {
		$wrapper.classList.remove(errorState);

		if ($success) {
			$success.classList.add('subscription_success--open_state');
		}
	};

	$fields.forEach(($field) => {
		const $trigger = $field.querySelector('.subscriptionFormSubmit');
		const $input = $field.querySelector('.subscriptionFormInput');
		const placeholder = $input.getAttribute('placeholder');

		$input.addEventListener('input', () => {
			if ($input.value !== '') {
				$field.classList.add(typingState);
			} else {
				$input.setAttribute('placeholder', placeholder);
				$field.classList.remove(errorState);
				$field.classList.remove(typingState);
			}
		});

		$trigger.addEventListener('click', (e) => {
			if (isValid(e, $input) !== null) {
				handleSubmit($field);
			} else {
				throwError($field, $input);
			}
		});
	});
};

export default subscriptionForm;
