import { onWindowResize } from 'utils/index';
import { GLOBAL_VARS } from 'utils/constants';
import Swiper, { Navigation, Pagination } from 'swiper';
import buildSliders from './buildSliders';

const LatestArticlesSlider = () => {
	// console.log('slider'); //!
	const sliderClass = '.latestArticlesSlider';
	const currentSlider = document.querySelector(sliderClass);
	const sliderArrows = document.querySelector('.latest_articles__navigation');
	const sliderProgress = document.querySelector('.latest_articles_bottom');
	let isMobile;
	let isTablet;
	const checkIsMobile = () => {
		isMobile = window.innerWidth < GLOBAL_VARS.mediaPoint2;
		isTablet = window.innerWidth < GLOBAL_VARS.mediaPoint1;
	};

	onWindowResize(checkIsMobile);
	buildSliders(sliderClass);

	let slideEl = document.querySelectorAll(sliderClass);
	let sliderLength = document.querySelectorAll('.latestArticlesSlider .swiper-slide:not(.swiper-slide-duplicate)').length;

	setTimeout(() => {
		if (typeof (slideEl) !== 'undefined' && slideEl != null) {
			const $currentSlideIndex = document.querySelector('.latestArticlesCurrent');
			const $totalSlideIndex = document.querySelector('.latestArticlesTotal');

			let latestArticlesSliderEl = new Swiper(sliderClass, {
				modules: [Navigation, Pagination],
				observer: true,
				observeParents: true,
				speed: 800,

				navigation: {
					nextEl: '.latest_articles__navigation .latest_articles__arrow--next',
					prevEl: '.latest_articles__navigation .latest_articles__arrow--prev',
				},

				pagination: {
					el: '.latestArticlesProgress',
					type: 'progressbar',
					clickable: true,
				},

				breakpoints: {
					320: {
						slidesPerView: 1,
					},
					768: {
						slidesPerView: 1.5,
					},
					1024: {
						slidesPerView: 3,
						loop: false,
					},
					1280: {
						slidesPerView: 4,
						loop: true,
					},
				},

				on: {
					init: (slider) => {
						const currentSlider = document.querySelector(sliderClass);
						const index = currentSlider.querySelectorAll('.swiper-slide:not(.swiper-slide-duplicate)').length;
						$totalSlideIndex.innerHTML = index >= 10 ? index : `0${index}`;
					},
					slideChange: (slider) => {
						const index = slider.realIndex + 1;
						$currentSlideIndex.innerHTML = index >= 10 ? index : `0${index}`;
					},
				},
			});
			if ((!isTablet && sliderLength < 5) || (isTablet && sliderLength < 2)) {
				latestArticlesSliderEl.destroy();
				currentSlider.classList.add('is-destroy');
				sliderArrows.classList.add('hidden');
				sliderProgress.classList.add('hidden');
			} else {
				latestArticlesSliderEl.init();
				currentSlider.classList.remove('is-destroy');
				sliderArrows.classList.remove('hidden');
				sliderProgress.classList.remove('hidden');
			}
		}
	});
};

export default LatestArticlesSlider;
